<template>
    <div class="sidebar">
        <!-- <el-menu :default-active="onRoutes" class="el-menu-vertical-demo" theme="dark" unique-opened router>
            <el-menu-item index="readme">
                <i class="el-icon-setting"></i>自述
            </el-menu-item>
            <el-submenu index="2">
                <template slot="title"><i class="el-icon-menu"></i>vue主要插件</template>
                <el-menu-item index="router">vue-router</el-menu-item>
                <el-menu-item index="resource">vue-resource</el-menu-item>
                <el-menu-item index="vuex">vuex</el-menu-item>
            </el-submenu>
        </el-menu> -->
        <el-menu default-active="onRoutes" class="el-menu-vertical-demo" theme="dark" unique-opened router>
            <el-menu-item index="readme">
                <i class="el-icon-setting"></i>自述
            </el-menu-item>
            <el-submenu index="2">
                <template slot="title"><i class="el-icon-menu"></i>vue主要插件</template>
                <el-menu-item index="router">vue-router</el-menu-item>
                <el-menu-item index="resource">vue-resource</el-menu-item>
                <!-- <el-submenu index="vuex">
                    <template slot="title">vuex</template>
                    <el-menu-item index="demo">demo</el-menu-item>
                </el-submenu> -->
                <el-submenu index="vuex">
                    <template slot="title">vuex</template>
                    <el-menu-item v-for="value in arr" :index="value">{{value}}</el-menu-item>
                </el-submenu>
            </el-submenu>
        </el-menu>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                arr: ['one', 'two']
            }
        },
        computed:{
            onRoutes(){
                return this.$route.path.replace('/','');
            }
        }
    }
</script>

<style scoped>
    .sidebar{
        display: block;
        position: absolute;
        width: 250px;
        left: 0;
        top: 70px;
        bottom:0;
        background: #2E363F;
    }
    .sidebar > ul {
        height:100%;
    }
</style>



